import React, {Component} from 'react';


export default class Grid extends Component{

    constructor(props){
        super(props);

        this.state = {
            grid: props.gridArray,
            rows: 10,
            column: 10,
        };
    }

    makeGrid(array){
        let grid = this.state.grid;

        array.map( (item,i)=>{
            grid.push(
                <div key={i} className="cell">
                    {item}
                </div>);
        });
        return grid;
    }

    render(){
        let array = [1,2,3,4,5,6];
        let grid = this.makeGrid(array);
        return(
            <div className="Grid">
                {
                   grid
                }
            </div>
        );
    }
}